'use strict';

angular.module('adf.widget.pdfviewer', ['adf.provider'])
    .directive('pdfViewer', function($sce){
        return {
            restrict: 'AE',
            scope: {
                src: '='
            },
            template: '<div ng-show="show" id="gdvId" class="gdocsviewer"><iframe ng-src="{{getUrl()}}" width="{{settings.width}}" height="{{settings.height}}"></iframe></div>',
            link: function(scope, element, attrs){
                scope.settings = {
                    width  : '600',
                    height : '700'
                };

                var file = scope.src;
                var ext = file.substring(file.lastIndexOf('.') + 1);
                scope.gdvId = scope.$id;
                scope.show = false;

                scope.getUrl = function(){
                    if (/^(tiff|pdf|ppt|pps|doc|docx|xls|xlsx|jpg|png)$/.test(ext)) {
                        scope.show = true;
                    }
                    return $sce.trustAsResourceUrl('http://impress.app/Plugin/KbCore/views/pdf/index.php?file=' + scope.src);
                }


            }
        }
    })
    .config(function(dashboardProvider){
        dashboardProvider
            .widget('pdfviewer', {
                title: 'PDF',
                description: 'Exibir PDFs',
                category: 'Relatórios',
                reload: true,
                templateUrl: '../src/templates/widgets/PdfView/view.html',
                edit: {
                    templateUrl: '../src/templates/widgets/PdfView/edit.html',
                    controller: 'PdfViewEditCtrl'
                }
            });
    }).controller('PdfViewEditCtrl', function($scope){

    function getPdfs(){
        if (!$scope.config.pdfs){
            $scope.config.pdfs = [];
        }
        return $scope.config.pdfs;
    }

    $scope.addLink = function(){
        getPdfs().push({});
    };

    $scope.removeLink = function(index){
        getPdfs().splice(index, 1);
    };
});/*
 * jQuery.gdocViewer - Embed linked documents using Google Docs Viewer
 * Licensed under MIT license.
 * Date: 2011/01/16
 *
 * @author Jawish Hameed
 * @version 1.0
 */
(function($){
    $.fn.gdocsViewer = function(options) {

        var settings = {
            width  : '600',
            height : '700'
        };

        if (options) {
            $.extend(settings, options);
        }

        return this.each(function() {
            var file = $(this).attr('href');
            var ext = file.substring(file.lastIndexOf('.') + 1);

            if (/^(tiff|pdf|ppt|pps|doc|docx|xls|xlsx)$/.test(ext)) {
                $(this).after(function () {
                    var id = $(this).attr('id');
                    var gdvId = (typeof id !== 'undefined' && id !== false) ? id + '-gdocsviewer' : '';
                    return '<div id="' + gdvId + '" class="gdocsviewer"><iframe src="http://docs.google.com/viewer?embedded=true&url=' + encodeURIComponent(file) + '" width="' + settings.width + '" height="' + settings.height + '" style="border: none;"></iframe></div>';
                })
            }
        });
    };
})( jQuery );